'use client';
import { useEffect, useState } from 'react';
import { client } from '../../client';
import { Playlist, Track } from '@spotify/web-api-ts-sdk';
import Image from 'next/image';
import { Skeleton } from '@/components/ui/skeleton';

export default function PlaylistDetail({ params }: { params: { id: string } }) {
	const [playlist, setPlaylist] = useState<Playlist | null>(null);
	const [loading, setLoading] = useState(true);

	useEffect(() => {
		const getPlaylist = async () => {
			try {
				const response = await client.playlists.getPlaylist(params.id);
				setPlaylist(response);
			} catch (error) {
				console.error('Failed to fetch playlist:', error);
			} finally {
				setLoading(false);
			}
		};
		getPlaylist();
	}, [params.id]);

	if (loading) {
		return (
			<div className="space-y-6">
				<div className="flex gap-6 items-end">
					<Skeleton className="h-48 w-48" />
					<div className="space-y-2">
						<Skeleton className="h-8 w-48" />
						<Skeleton className="h-4 w-24" />
					</div>
				</div>
				{[...Array(5)].map((_, i) => (
					<Skeleton key={i} className="h-16 w-full" />
				))}
			</div>
		);
	}

	if (!playlist) return null;

	return (
		<div className="space-y-6">
			<div className="flex gap-6 items-end">
				<Image
					src={playlist.images[0]?.url || '/playlist-placeholder.png'}
					alt={playlist.name}
					width={192}
					height={192}
					className="rounded-lg shadow-lg"
				/>
				<div>
					<h1 className="text-4xl font-bold text-white">{playlist.name}</h1>
					<p className="text-neutral-400">{playlist.tracks.total} tracks</p>
				</div>
			</div>

			<div className="space-y-2">
				{playlist.tracks.items.map((item, index) => {
					const track = item.track as Track;
					return (
						<div
							key={track.id}
							className="flex items-center gap-4 p-2 rounded-lg hover:bg-white/5 transition-colors group"
						>
							<div className="w-10 text-neutral-400 text-right">
								{index + 1}
							</div>
							<Image
								src={track.album.images[0]?.url || '/track-placeholder.png'}
								alt={track.name}
								width={40}
								height={40}
								className="rounded"
							/>
							<div className="flex-grow min-w-0">
								<div className="text-white truncate group-hover:text-emerald-400">
									{track.name}
								</div>
								<div className="text-neutral-400 text-sm truncate">
									{track.artists.map(a => a.name).join(', ')}
								</div>
							</div>
							<div className="text-neutral-400">
								{formatDuration(track.duration_ms)}
							</div>
						</div>
					);
				})}
			</div>
		</div>
	);
}

function formatDuration(ms: number): string {
	const minutes = Math.floor(ms / 60000);
	const seconds = Math.floor((ms % 60000) / 1000);
	return `${minutes}:${seconds.toString().padStart(2, '0')}`;
}
